body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.manual-container {
  width: auto;
  max-width: 680px;
  padding: 0 15px;
}
.navbar-nav span[role=button] {
  padding: 15px;
  display: inline-block;
  line-height: 20px;
}

.navbar {
  margin-bottom: 0;
}

#brand {
  color: #f5f5f5;
  font-size: x-large;
}

.navbar > .container {
  width: 100%;
}

.nav-links {
  color: #9d9d9d;
}

.footer-text {
  color: #6c757d!important;
}

.footer {
  width: 100%;
  background-color: #f5f5f5;
  margin-top: 3em;
  padding: 1em 1em 1em 1em;
}

.footer-star {
  padding-top: 12%;
}

#footer-ul {
  float: right;
}

#footer-links {
  float: right;

}
.jumbotron-para {
    margin-top: 0;
    font-size: xx-large!important;
}

.div-panel {
    background-color: #5a5a5a;
    margin-top: 3em;
    margin-bottom: 3em;
}

.team-intro {
    margin-top: 2em;
    margin-bottom: 2em;
}

.panel-heading {
    text-align: center;
    color: #f5f5f5;
    font-size: 3.5em;
}

.normal-para {
    font-size: larger;
    font-weight: 200;
}

.rounded-circle {
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%!important;
    width: 140px;
    height: 140px;
}

.team-names {
    text-align: center;
}

img {
    vertical-align: middle;
    border-style: none;
}
.form-group {
    margin-left: 1em!important;
    margin-right: 1em!important;
}

.run-button {
    display: inline-block;
    width: 50%;
    height: 3em;
    margin-left: 0!important;
    /* margin-top: 1em; */
    font-size: 4em;
}

.forms-buttons {
    display: flex;
}

.prediction-col {
    font-size: larger;
    font-weight: 200;
}

.rating-jumbo {
    margin-top: 1em;
}

.button-grouping {
    width:100%!important;
    margin-top:3em!important;
    margin-bottom:0!important;
}
.results-jumbo {
    margin-top: 2em;
}
img {
    margin: 1.5em 0;
    width: 100%;
}

#data-prep-jumbo {
    margin-top: 2.5em;
}
